import React, { useState } from "react"
import { useKeenSlider } from "keen-slider/react"
import "keen-slider/keen-slider.min.css"
import "./styles.css"
import Slider1 from "./Slider1"

const JourneySlider = () => {
    const [currentSlide, setCurrentSlide] = useState(0)
    const [loaded, setLoaded] = useState(false)
    const [sliderRef, instanceRef] = useKeenSlider({
        initial: 0,
        slideChanged(slider) {
            setCurrentSlide(slider.track.details.rel)
        },
        created() {
            setLoaded(true)
        },
    })

    return (
        <>
            <div className="navigation-wrapper" id="nfts">
                {/* {loaded && instanceRef.current && ( */}
                {/* <> */}
                <div className="slider-arrow" onClick={(e) =>
                    e.stopPropagation() || instanceRef.current?.prev()
                }>
                    <Arrow
                        left
                        disabled={currentSlide === 0}
                    />
                </div>
                {/* </> */}
                {/* )} */}
                <div ref={sliderRef} className="keen-slider">
                    <Slider1 />
                    <div className="keen-slider__slide number-slide2">2</div>
                </div>
                {/* {loaded && instanceRef.current && ( */}
                {/* <> */}
                <div className="slider-arrow" onClick={(e) =>
                    e.stopPropagation() || instanceRef.current?.next()
                }>
                    <Arrow
                        disabled={
                            currentSlide ===
                            instanceRef?.current?.track.details.slides.length - 1
                        }
                    />
                </div>
                {/* </> */}
                {/* )} */}
            </div>
            {/* {loaded && instanceRef.current && (
                <div className="dots">
                    {[
                        ...Array(instanceRef.current.track.details.slides.length).keys(),
                    ].map((idx) => {
                        return (
                            <button
                                key={idx}
                                onClick={() => {
                                    instanceRef.current?.moveToIdx(idx)
                                }}
                                className={"dot" + (currentSlide === idx ? " active" : "")}
                            ></button>
                        )
                    })}
                </div>
            )} */}
        </>
    )
}

function Arrow(props) {
    const disabeld = props.disabled ? " arrow--disabled" : ""
    return (
        <svg
            onClick={props.onClick}
            className={`arrow ${props.left ? "arrow--left" : "arrow--right"
                } ${disabeld}`}
            xmlns="http://www.w3.org/2000/svg"
            width="24.614" height="41.309" viewBox="0 0 24.614 41.309"
        >
            {props.left && (
                <g id="Group_91" data-name="Group 91" transform="translate(24.614 41.309) rotate(180)">
                    <path id="Icon_ionic-ios-arrow-back" data-name="Icon ionic-ios-arrow-back" d="M7.119,20.649,22.75,5.031A2.952,2.952,0,0,0,18.568.862L.86,18.559a2.946,2.946,0,0,0-.086,4.071L18.556,40.449a2.952,2.952,0,1,0,4.181-4.169Z" transform="translate(24.614 41.309) rotate(180)" fill="#fff" />
                    <path id="Icon_ionic-ios-arrow-back-2" data-name="Icon ionic-ios-arrow-back" d="M3.5,10.152l7.685-7.679A1.451,1.451,0,0,0,9.129.424L.423,9.124a1.449,1.449,0,0,0-.042,2l8.743,8.761a1.451,1.451,0,1,0,2.056-2.05Z" transform="translate(11.611 30.809) rotate(180)" fill="#fff" />
                </g>
            )}
            {!props.left && (
                <g id="Group_93" data-name="Group 93" >
                    <path id="Icon_ionic-ios-arrow-back" data-name="Icon ionic-ios-arrow-back" d="M7.119,20.649,22.75,5.031A2.952,2.952,0,0,0,18.568.862L.86,18.559a2.946,2.946,0,0,0-.086,4.071L18.556,40.449a2.952,2.952,0,1,0,4.181-4.169Z" transform="translate(24.614 41.309) rotate(180)" fill="#fff" />
                    <path id="Icon_ionic-ios-arrow-back-2" data-name="Icon ionic-ios-arrow-back" d="M3.5,10.152l7.685-7.679A1.451,1.451,0,0,0,9.129.424L.423,9.124a1.449,1.449,0,0,0-.042,2l8.743,8.761a1.451,1.451,0,1,0,2.056-2.05Z" transform="translate(11.611 30.809) rotate(180)" fill="#fff" />
                </g>
            )}
        </svg>
    )
}

export default JourneySlider;