import React, { useState, useEffect } from 'react';
const Footer = () => {
    const [scrollPos, SetScrollPos] = useState(0);
    const scrollToTop = () => window.scrollTo(0, 0);
    useEffect(() => {
        function updatePosition() {
            SetScrollPos(window.pageYOffset);
        }
        window.addEventListener('scroll', updatePosition);
        return () => window.removeEventListener('scroll', updatePosition);
    }, []);
    return (
        <div className='footer'>
            <span className="elementor-divider-separator">
                <div className="elementor-icon elementor-divider__element">
                    <img src="/img/logo.png" alt="" />
                </div>
            </span>
            <div className='section section-6'>
                <div className='logo-name'>the<br /> mandrills</div>
                <div className='year'> 2023</div>
                <div className='footer-section'>
                    <div className='info'>Powered by Wild Network!</div>
                    <a className='privacy' href="#">Privacy and Policy</a>
                    <a href='#'>Terms of Service</a>
                    <div className='contact-group'>
                        <a href='#' className='discord'>
                            <img src="/img/discord.png" alt="" />
                        </a>
                        <a href='#' className='medium'>
                            <img src="/img/medium.png" alt="" />
                        </a>
                        <a href='#' className='twitter'>
                            <img src="/img/twitter.png" alt="" />
                        </a>
                        <a href='#' className='youtube'>
                            <img src="/img/youtube.png" alt="" />
                        </a>
                    </div>
                </div>
            </div>
            {scrollPos > 100 ? <img className='scrollto' src='/img/up.png' alt="" onClick={scrollToTop} /> : <></>}
        </div>
    )
}

export default Footer;